.list {
  display: flex;
  gap: 90px;
  flex-wrap: wrap;
}

.containerItem {
  display: inline-flex;
  flex-direction: column;
  background: linear-gradient(
    114.99deg,
    #471ca9 -0.99%,
    #5736a3 54.28%,
    #4b2a99 78.99%
  );
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  border-radius: 20px;
  /* width: 380px;
  height: 460px; */

  align-items: center;
  /* justify-content: center; */
}

.upperPart {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  background-image: url(../images/bcgImagGOIT.png);

  width: 308px;
  height: 168px;
  margin-left: 36px;
  margin-right: 36px;
  margin-top: 8px;
  margin-bottom: 18px;
}

.imageLogo {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  background-image: url(../images/Logo.png);
  width: 72px;
  height: 22px;
  border-style: none;
  background-color: transparent;

  position: relative;
  top: 20px;
  left: 20px;
}

.middleLine {
  background: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
    inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
  height: 8px;
}

/* .space {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
} */

.userFoto {
  position: relative;
  top: -32px;
  left: 160px;
  width: 62px;
  height: 62px;
  border-radius: 50%;
  background-color: #ebd8ff;

  padding: 9px;
  box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06),
    inset 0px -2.19582px 4.39163px #ae7be3,
    inset 0px 4.39163px 3.29372px #fbf8ff;
}

.lowerPart {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 62px;
}

.ps {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;

  color: #ebd8ff;
  margin-bottom: 16px;
}
